import { connect } from 'react-redux';
import { MessagesList } from './MessagesList';
import { getMessages, deleteMessage } from '../../store/DialogsReducer';
import { compose } from 'redux';


const mapStateToProps = (state) => ({	
	messages: state.DialogsReducer.messagesArray
})
	 

export const MessagesListContainer = compose(connect(mapStateToProps, { getMessages, deleteMessage }))(MessagesList)


