import React from 'react';
import dialog from '../Dialogs/Dialogs.module.css';
import { useEffect } from 'react';
import { Bucket } from '../common/Buttons/Bucket/Bucket';


export const MessagesList = (props) => {
	const { id, getMessages } = props;

	useEffect(()=>{
		id && getMessages(id)
	},[id, getMessages])
	return (
			<div  className = {dialog.MessagesField}>
				{props.messages.map((item,index) =><div className = {dialog.messageItem} 
															key = {index}>															
													{/* <div style = {{backgroundImage: `url(${item.photos.small || process.env.PUBLIC_URL+props.avatar})`}}></div> */}
													<div className="messageBody">
														<span >{item.senderName}</span>
														<p>{item.body}</p>
													</div>
													<Bucket onclick = {()=>props.deleteMessage(item.id, props.id)}/>
												</div> )}
			</div>			
		)	
}
